import { useHistory } from 'react-router-dom'
import { useEffect } from 'react'

import '../styles/Jumbotron.css'


const Jumbotron = () => {
    console.log('jumbotron rendered')
    const history = useHistory()

    // slow down the video a bit
    useEffect(() => {
        let vid = document.getElementById("JumbotronVideo")
        vid.playbackRate = 0.7   
    })


    return(
        <div className="JumbotronContainer">
            <video autoPlay muted loop id="JumbotronVideo">
                <source src="https://good-store-resources.s3-us-west-2.amazonaws.com/resources/assets/dope-background.mp4" type="video/mp4"/>
            </video>
            <div className="VideoOverlayContainer">
                <span className="JumbotronMessage">The Goods</span>
                <span onClick={() => history.push('/shop')} className="ShopButton" >Shop</span>
            </div>
        </div>
    )
}



export default Jumbotron