import { useState } from 'react'

import axios from 'axios'
import { loadStripe } from "@stripe/stripe-js"

import '../styles/Cart.css'
import { API_URL, BUCKET } from '../utils/Variables'

const stripePromise = loadStripe("pk_test_51HEhAgAX9HHAr5HrTmBPQ9cPS7oGlGOfI2cNYOADmRtCsUmmitjAdrBrsDA2Ne7l1TYzvvfUWbMAebEQmJ1vzuhh00uzyGHwpt")

const Cart = props => {
    console.log('cart rendered')

    const [error, setError] = useState('')
    
    // add up cart subtotal and round it to nearest 1/100th
    let cartSubTotal = 0

    props.cart.forEach(cartItem => {
        cartSubTotal += (cartItem.price * cartItem.qty)
    })

    cartSubTotal = cartSubTotal.toFixed(2)

    const redirectToCheckout = async () => {
        props.setLoading(true)

        const stripe = await stripePromise

        axios.post(
            `${API_URL}/create-checkout-session`,
            { cart: props.cart }
        ).then(response => {
            if (response.data.success) {
                localStorage.setItem('Cart', JSON.stringify(props.cart))
                stripe.redirectToCheckout({ sessionId: response.data.id }).then(res =>{
                    console.log('response from create checkout session=', res)
                }).catch(err => {
                    console.log(err)
                    setError(err.message)
                })
            } else {
                setError(response.data.message)
                console.log('error creating checkout session', response.data.message) // error handling <================================ TODO
            }
            props.setLoading(false)
        }).catch(error => {
            setError(error.message)
            props.setLoading(false)
            console.log(error)  // error handling <================================ TODO
        })
    }

    return(
        <div className="PageContainer">                
            <h1 className="PageTitle">Cart</h1>
            <div className="CartContainer">
                { error && <h1>{ error }</h1>}
                { props.cart.length === 0 && <p>You have no items in your cart!</p> }

                { 
                    props.cart && props.cart.map(item => (
                        <div key={item.slug} className="CartItem">
                            <div className="CartItemImage">
                                <img src={`${BUCKET}/product_images/${item.slug}/1.jpeg`} alt='item' width='100%' />
                            </div>
                            <h3 className="CartItemName">{ item.name }</h3>

                            <div className="QtyAndPriceContainer">
                                <p className="CartItemPrice">${item.price.toFixed(2)}</p>

                                <div className="QtyAdjustmentContainer">
                                    <span className="QtyAdjustmentButton" onClick={() => props.adjustQty(item, 'down')}>&#8610;</span>
                                    <p className="QtyLabel">{ item.qty }</p>
                                    <span className="QtyAdjustmentButton" onClick={() => props.adjustQty(item, 'up')}>&#8611;</span>
                                </div>
                            </div>

                        </div>
                    ))
                }

            { cartSubTotal > 0 && <p className="CartSubTotal"><em>Sub Total: </em><strong>${cartSubTotal}</strong></p> }

            </div>

            { props.cart.length > 0 && <span className="Button CheckoutButton" onClick={() => redirectToCheckout()}>Checkout</span> }          
        </div>

    )
}

export default Cart


