import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

import { API_URL, BUCKET } from '../utils/Variables'

import '../styles/Shop.css'

const Shop = props => {
    const [products, setProducts] = useState()
    const [error, setError] = useState('')

    useEffect(() => {
        console.log("Using Effect - Shop.js")
        props.setLoading(true)

        axios.get(`${API_URL}/products`).then(response => {
            setProducts(response.data)
            props.setLoading(false)
        }).catch(error => {
            console.log(error.message)
            setError(error.message)
            props.setLoading(false)
        })            
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return(
        <div>
            <h1 className="PageTitle">Products</h1>

            <div className="ProductsContainer">
                { products && products.map(product => (
                    <Link 
                        to={`/shop/${product.slug}`}
                        key={product.slug}
                        className='ProductThumbnail'
                    >
                        <img className="ProductImage" src={`${BUCKET}/product_images/${product.slug}/${product.images[0]}`} alt='product' />
                        <h1>{product.name}</h1>
                        <p>${product.price}</p>
                        <p className="ProductDescription">{product.description}</p>
                    </Link>
                ))}

                { error &&  <p>Uh oh! There seems to be something wrong with our servers... { error }</p> }
            </div>
        </div>

    )
}

export default Shop