import axios from 'axios'
import { useState, useEffect } from 'react'

import '../styles/Product.css'
import { API_URL, BUCKET } from '../utils/Variables'

import FullScreenImage from '../components/FullScreenImage'

const Product = props => {
    const [qty, setQty] = useState(1)
    const [selectedImage, setSelectedImage] = useState(null)
    const [images, setImages] = useState([])
    const [product, setProduct] = useState([])
    const [showFullScreenImg, setShowFullScreenImg] = useState(false)

    useEffect(() => {
        props.setLoading(true)

        axios.get(`${API_URL}/products/${props.slug}/`).then(response => {
            let imgs = []

            response.data.images.forEach((img, idx) => {
                imgs.push(<img src={`${BUCKET}/product_images/${product.slug}/${img}`} className='productImages' alt='product' width="100%" /> )
            })

            setImages(imgs)
            setSelectedImage(imgs[0])
            setProduct(response.data)
            props.setLoading(false)
        }).catch(error => {
            props.setLoading(false)
            console.log(error)
        })
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [product.slug])


    const adjustQty = direction => {
        if (direction === 'up') {
            setQty(qty + 1)
        } else {
            if (qty > 1) {
                setQty(qty - 1)
            }
        }
    }

    return(
        <div className="ProductContainer PageContainer">                
            <h1 className="PageTitle">{product.name}</h1>

            <div>

                { showFullScreenImg && <FullScreenImage image={selectedImage} setShowFullScreenImg={setShowFullScreenImg} /> }

                <div className="ProductImagesContainer">
                    <div 
                        className="SelectedProductImage"
                        onClick={() => setShowFullScreenImg(true)}
                    >
                        { selectedImage }
                    </div>

                    <div className="ThumbnailsContainer">
                        { images.length > 1 &&
                            images.map((image, idx) => (
                                        <div 
                                            className="ThumbnailImageContainer" 
                                            key={idx}
                                            onClick={() => setSelectedImage(image)}
                                        >
                                            {image}
                                        </div>

                            ))
                        }
                    </div>
                </div>

                <div className="ProductInfoContainer">
                    <p className="ProductDescription">{product.description}</p>         
                    <h1>${product.price}</h1>
                    <div className="QtyAdjustmentContainer">
                        <span className="QtyAdjustmentButton" onClick={() => adjustQty('down')}>&#8610;</span>
                        <p className="QtyLabel">qty: <span>{qty}</span></p>
                        <span className="QtyAdjustmentButton" onClick={() => adjustQty('up')}>&#8611;</span> 
                    </div>
                    <span className="Button AddToCartButton" onClick={() => props.addItemToCart(product, qty)}>Add to Cart</span>                
                </div>

            </div>            
        </div>

    )
}

export default Product